<template>
  <div class="dashboard">
    <div class="allert__mes" v-if="copyDone == true">
      <b-message type="is-success" has-icon auto-close :duration="600"> </b-message>
    </div>

    <b-field grouped group-multiline>
      <div class="container--conrolers">
        <div class="control control--border">
          <b-switch v-model="isBordered">
            <span class="total__title"> </span>
          </b-switch>
        </div>
        <div class="total__pool-reward">
          <span class="total__title">
                 -
            <span
              class="total__title--pool-reward"
            >{{totalPoolReward}}</span>
          </span>
        </div>
      </div>
    </b-field>
    <hr />
    
    <section class="filter__container">
      <div class="block filter__conrols">
        <b-radio 
          v-model="typeofFiltering"
          name="type"
          native-value="period">
          
        </b-radio>
        <b-radio 
          v-model="typeofFiltering"
          name="type"
          native-value="oneday">
           
        </b-radio>
      </div>
      <div class="filter__datapicker" v-if="typeofFiltering == 'period'">
        <b-field>
                <b-button
                    @click="$refs.datepicker.toggle()"
                    icon-left="calendar-today"
                    type="is-primary" />
                <b-datepicker
                    placeholder="  ..."
                    v-model="checkedPeriods"
                    range
                    :min-date="periods[0] !== undefined ? new Date (new Date(periods[0].start).setHours(0)) : new Date()"
                    :max-date="new Date()"
                    ref="datepicker"
                    >
                </b-datepicker>
        </b-field>
      </div>

      <div class="filter__datapicker" v-if="typeofFiltering == 'oneday'">
        <b-field>
                <b-button
                    @click="$refs.datepicker.toggle()"
                    icon-left="calendar-today"
                    type="is-primary"/>
                <b-datepicker
                    placeholder="  ..."
                    v-model="checkedPeriods"
                    :min-date="periods[0] !== undefined ? new Date (new Date(periods[0].start).setHours(0)) : new Date()"
                    :max-date="new Date()"
                    ref="datepicker"
                    >
                </b-datepicker>
        </b-field>
      </div>

      <b-button type="is-primary is-light" @click="getFullPeriods"> </b-button>
    </section>
    <hr />

    <div class="table__container" @mouseup="selected.length > 1 ? multiplyFunction() : null">
      <section>
        <b-table
          :data="data == [] ? [] : data"
          :bordered="isBordered"
          :striped="true"
          :mobile-cards="true"
          :row-class="() => 'dashboard__row'"
          default-sort="id"
          @sort="numDataClear"
        >
          <b-table-column field="id" label="ID" sortable width="40" numeric v-slot="props">
            <div class="selected--none">{{ props.row.id }}</div>
          </b-table-column>

          <b-table-column
            field="alias"
            :centered="false"
            label="Alias"
            cell-class="column--fixed"
            :subheading="miningPoolAlias"
            sortable
            searchable
          >
            <template slot="searchable" slot-scope="props">
              <b-input
                v-model="props.filters[props.column.field]"
                placeholder=""
                icon="magnify"
                size="is-small"
                custom-class="input__search"
              />
            </template>
            <template v-slot="props">
              <div @click="copyToBuff( props.row.alias )" class="dashboard__alias">
                <div class="selected--none">{{ props.row.alias }}</div>
              </div>
            </template>
          </b-table-column>

          <b-table-column
            field="asset"
            :centered="false"
            label="Asset"
            subheading="0xfbebbdf7278ecdc1cf6eb5679b679a4360c40ae7"
            searchable
          >
            <template slot="searchable" slot-scope="props">
              <b-input
                v-model="props.filters[props.column.field]"
                placeholder=""
                icon="magnify"
                size="is-small"
                custom-class="input__search"
              />
            </template>
            <template v-slot="props">
              <div
                class="dashboard__asset selected--none"
                @click="copyToBuff(props.row.asset)"
              >{{ props.row.asset }}</div>
            </template>
          </b-table-column>

          <b-table-column
            v-for="(period, num) in periods"
            :key="num"
            :label="toDate(period)"
            :field="`reward[${num}].reward`"
            centered
            :subheading="rewardforPool(period)"
            :visible="checkifFiltred(period)"
            cell-class='dasboadrd__periods-reward'
            sortable
            :custom-sort="sortTest"
          >
            <template v-slot:header="{ column }">
              <div class="period__sorted--container"  @click="sortedPeriod(num)">
                <div>{{ column.label }}</div>
                <b-icon icon="arrow_upward" both size="is-small" :class="[{ 'is-desc': !numisAsc }, 'period__sorted--arrow', 'period__sorted', {'period__sorted--visible': numData === num}]"></b-icon>
              </div>
            </template>
            <template v-slot="props">

              <div
                :class="[{ selected: checkingIfSelected(checkKey(props.row.id, num)) == true && checkThePayment(props.row.asset , period)}, checkThePayment(props.row.asset , period), 'selected--none']"
                @click="props.row.reward[num].reward !== 0 ? copyToBuff( props.row.reward[num].reward ) : null"
                @mousedown="select"
                @mouseover="select"
                @mouseup="selected.length > 1 ? null : singleFunction( checkThePayment(props.row.asset , period) )"
                :data-key="checkKey(props.row.id, num)"
                :data-asset="props.row.asset"
                :data-periodstart="period.start"
                :data-periodend="period.end"
                :data-reward="props.row.reward[num].reward"
              >{{props.row.reward[num].reward}}</div>

            </template>
          </b-table-column>

          <b-table-column
            sortable
            field="totel"
            :subheading="miningPoolFullIncome"
            label="Total"
            v-slot="props"
          >
            <div
              class="dashboard__total selected--none"
              @click="copyToBuff(rewardedPerAsset( props.row.asset ))"
            >{{ rewardedPerAsset(props.row.asset) }}</div>
          </b-table-column>
        </b-table>
      </section>
    </div>
  </div>
</template>

<script>
import * as firebase from "firebase/app";
require("firebase/database");

export default {
  data: function () {
    return {
      firebaseConfig: "",
      firstlineAssets: null,
      snapshot: null,
      periods: [],
      time: 0,
      lastSnapTime: 0,
      data: [],
      hasMobileCards: true,
      selected: [],
      mess:[],
      confirmPopUpTitle: '',
      confirmPopUpType: '',
      tokenPaindOrNot: null,
      copyDone: null,
      miningPoolFullIncome: 0,
      miningPoolAlias: '',
      numData: null,   
      numisAsc: true,
      
      //table val
      isBordered: false,
      defaultSortDirection: 'asc',
      sortIcon: 'arrow-up',
      sortIconSize: 'is-small',
      loader: null,
      selected: [],

      //filter
      checkedPeriods: [],
      filtredPeriods: [],
      typeofFiltering: 'period',
    };
  },
  methods: {
    toDate(el) {
      let startDate = new Date(el.start);
      let startMonth = startDate.getMonth()+1;
      let start =
        startDate.getDate() +
        "." +
        (startMonth < 10 ? "0" : "") +
        startMonth +
        "." +
        startDate.getFullYear();

      let endDate = new Date(el.end);
      let endMonth = endDate.getMonth()+1;
      let end =
        endDate.getDate() +
        "." +
        (endMonth < 10 ? "0" : "") +
        endMonth +
        "." +
        endDate.getFullYear();

      let result = `${start} ${end}`;
      return result;
    },
    rewardforPeriod(asset, period) {
      let checked = this.snapshot.find((el) => el.adress == asset);
      if (this.time > period.end) {
        if (checked !== undefined) {
          if (
            checked.rewardedPeriod !== undefined &&
            checked.rewardedPeriod !== 0
          ) {
            for (
              let i = 0;
              i <= Object.keys(checked.rewardedPeriod).length - 1;
              i++
            ) {
              let checkedEl =
                checked.rewardedPeriod[Object.keys(checked.rewardedPeriod)[i]];

              if (
                checkedEl.start === period.start &&
                checkedEl.end === period.end
              ) {
                return `${checkedEl.rewardAmount}`;
              } else if (i == Object.keys(checked.rewardedPeriod).length - 1) {
                return 0;
              }
            }
          } else {
            return 0;
          }
        } else {
          return 0;
        }
      } else {
        return "-";
      }
    },
    rewardedPerAsset(el) {
      let tempCheckedEl = this.snapshot.find((element) => element.adress == el);
      if (tempCheckedEl !== undefined) {
        let result = 0;
        for (
          let i = 0;
          i < Object.keys(tempCheckedEl.rewardedPeriod).length;
          i++
        ) {
          result =
            result +
            tempCheckedEl.rewardedPeriod[
              Object.keys(tempCheckedEl.rewardedPeriod)[i]
            ].rewardAmount;
        }

        this.data.find(element => {
          
          if(element.asset == el){
            let index = this.data.indexOf(element);
            if(index !== -1){ this.data[index].totel = result.toFixed(8);}
            
          }
        });
     
        return result.toFixed(8);
      }
    },
    rewardforPool(period) {
      let tempPeriodReward = period.poolReward;
      if (
        tempPeriodReward !== 0 &&
        tempPeriodReward !== undefined &&
        tempPeriodReward > 0
      ) {
        this.snapshot.forEach((el) => {
          for (let i = 0; i < Object.keys(el.rewardedPeriod).length; i++) {
            let tempRewPeriod =
              el.rewardedPeriod[Object.keys(el.rewardedPeriod)[i]];
            if (
              tempRewPeriod.start === period.start &&
              tempRewPeriod.end === period.end
            ) {
              tempPeriodReward = tempPeriodReward - tempRewPeriod.rewardAmount;
            }
          }
        });

        return tempPeriodReward.toFixed(8);
      } else if (tempPeriodReward !== undefined && this.time >= period.end) {
        return tempPeriodReward;
      } else {
        return "-";
      }
    },
    changeThePayment(el, period, tokenPaidOrNot) {
      console.log(el, period, tokenPaidOrNot);
      let checked = this.snapshot.find((element) => element.adress == el);

      if (checked !== undefined) {
        for (let i = 0; i < Object.keys(checked.rewardedPeriod).length; i++) {
          let tempChangeEl =
            checked.rewardedPeriod[Object.keys(checked.rewardedPeriod)[i]];

          if (
            tempChangeEl.start == period.periodstart &&
            tempChangeEl.end == period.periodend
          ) {
            tempChangeEl.paid = tokenPaidOrNot;
            if (!firebase.apps.length) {
              this.firebaseConfig = this.$store.getters.getfirebaseConfig;
              firebase.initializeApp(this.firebaseConfig);
            }

            let database = firebase.database();
            let poolClients = database.ref("/firstline");

            poolClients.once("value").then((snapshot) => {
              let tempSnap = snapshot.val();
              let tempData = [];
              let keyAdress = null;

              if (tempSnap !== 0) {
                for (let i = 0; i < Object.keys(tempSnap).length; i++) {
                  if (tempSnap[Object.keys(tempSnap)[i]].adress === el) {
                    keyAdress = Object.keys(tempSnap)[i];
                    let checkedAssetforCommit =
                      tempSnap[Object.keys(tempSnap)[i]];
                    for (
                      let i = 0;
                      i <
                      Object.keys(checkedAssetforCommit.rewardedPeriod).length;
                      i++
                    ) {
                      let tempRewPer =
                        checkedAssetforCommit.rewardedPeriod[
                          Object.keys(checkedAssetforCommit.rewardedPeriod)[i]
                        ];
                      if (
                        tempRewPer.start == period.periodstart &&
                        tempRewPer.end == period.periodend
                      ) {
                        let keyPeriod = Object.keys(
                          checkedAssetforCommit.rewardedPeriod
                        )[i];
                        if (
                          keyAdress !== null &&
                          keyAdress !== undefined &&
                          keyPeriod !== undefined
                        ) {
                          poolClients
                            .child(`/${keyAdress}`)
                            .child("/rewardedPeriod")
                            .child(`/${keyPeriod}`)
                            .update({ paid: tempChangeEl.paid });
                        }
                      }
                    }
                  }
                }
              }
            });
          }
        }
      }
    },
    checkThePayment(el, period) {
      let checked = this.snapshot.find((element) => element.adress == el);
      if (checked !== undefined) {
        for (let i = 0; i < Object.keys(checked.rewardedPeriod).length; i++) {
          let tempChangeEl =
            checked.rewardedPeriod[Object.keys(checked.rewardedPeriod)[i]];
          if (
            tempChangeEl.start === period.start && 
            tempChangeEl.end === period.end
          ) {
            if (tempChangeEl.paid === false && tempChangeEl.rewardAmount !== 0) {
              return "period__nonpaid";
            } else if (tempChangeEl.paid === true) {
              return "period__paid";
            }
          }
        }
      }
    },
    checkKey: (x,y) => `${y}.${x}`,
    select({ buttons, target: { dataset: { key, asset, periodstart, periodend, reward } }}) {
      if (buttons) {
        const checked = this.selected.find(el => el.id == key);
        if (checked !== undefined) {
          this.selected.splice(this.selected.indexOf(checked), 1);
        } else if(reward > 0){
          this.selected.push({ id: key, adress: asset, periodStart: periodstart, periodEnd: periodend, rewardForPeriod: reward });
        }
      }
    },
    checkingIfSelected(elementCoord){
      let checked = this.selected.find(el=> el.id == elementCoord);
      if(checked !== undefined){return true}else{return false};
    },
    confirmTransfers(statusToken){
      this.$buefy.dialog.confirm({
                    title: `${this.confirmPopUpTitle}`,
                    message: `${this.messageConstructor()}`, 
                    cancelText: '',
                    confirmText: '',
                    
                    type: `${this.confirmPopUpType}`,
                    onConfirm: () => {

                      if(statusToken == 'multiply'){
                        let tempChouseOption = this.checkRadio();
                        switch(tempChouseOption){
                          case 'true': this.tokenPaindOrNot = true; break;
                          case 'false': this.tokenPaindOrNot = false; break;
                        }
                      }
                                
                      this.mess.forEach(el=>{
                        el.periods.forEach(period =>{
                          const token = this.tokenPaindOrNot;
                          console.log(token);
                          token !== null ? this.changeThePayment(el.asset, period, token) : null;
                        })
                      })        
                      this.mess = [];

                      this.tokenPaindOrNot == true 
                      ? this.$buefy.toast.open({
                        duration: 500,
                        message: ` `})
                      
                      : this.$buefy.toast.open({
                        duration: 500,
                        message: ` `});

                      this.tokenPaindOrNot = null;
                    },
                    onCancel: () => {this.mess = [];}
                })
    },
    singleFunction(statusPaid){  
      if(this.selected.length > 0){
        if(statusPaid == 'period__nonpaid'){

           this.mess.push({ asset: this.selected[0].adress, periods:[{periodstart:this.selected[0].periodStart, periodend: this.selected[0].periodEnd, reward: this.selected[0].rewardForPeriod}]});
           this.confirmPopUpTitle = '  ';
           this.confirmPopUpType = 'is-success';
           this.tokenPaindOrNot = true;
           this.confirmTransfers();

        }else if(statusPaid == 'period__paid'){

           this.mess.push({ asset: this.selected[0].adress, periods:[{periodstart:this.selected[0].periodStart, periodend: this.selected[0].periodEnd, reward: this.selected[0].rewardForPeriod}]});
           this.confirmPopUpTitle = '  ';
           this.confirmPopUpType = 'is-danger';
           this.tokenPaindOrNot = false;

           let statusToken = 'single';
           this.confirmTransfers(statusToken);
        } 
      }
    },
    multiplyFunction(){
      this.selected.forEach(el => {
        if(this.selected.length > 0) {

          let checkedIndex = -1;

          this.mess.forEach((element, index) => {
            if(element.asset == el.adress){
            checkedIndex = index;
          }});

          if(checkedIndex !== -1){
            this.mess[checkedIndex].periods.push({periodstart:el.periodStart, periodend: el.periodEnd, reward: el.rewardForPeriod});
          }else{
            this.mess.push({ asset: el.adress, periods:[{periodstart:el.periodStart, periodend: el.periodEnd, reward: el.rewardForPeriod}]});
          }
        }   
      })
      this.confirmPopUpTitle = '   ';
      this.confirmPopUpType = 'is-success';

      let statusToken = 'multiply';
      this.confirmTransfers(statusToken);
    },
    messageConstructor(){
      let row = [];
      let tempMess = this.mess.forEach((el,index) =>{
        let header = `<h1 class="confirm__asset">${el.asset}</h1>`;
        let total = 0;
        let periodsData = [];

        for(let i = 0; i < el.periods.length; i++){

          let starting = parseInt(el.periods[i].periodstart);
          let ending = parseInt(el.periods[i].periodend);
          let sendEl = {start: starting, end: ending};
          total = total + parseFloat(el.periods[i].reward);

          periodsData.push(`
          <h3 class="confirm__period">
            <div class="confirm__period--title">${this.toDate(sendEl).replace(' ', ' - ')} </div>
            <div class="confirm__period--amount">${el.periods[i].reward}</div>
          </h3>`);
        }
        const totalString = `
        <h3 class="confirm__period-total">
          <div class="confirm__period-total--title">Total:</div>
          <div class="confirm__period-total--amount">${total.toFixed(8)}</div>
        </h3>`
      
        row.push(`${header + periodsData.join("") + totalString} <hr>`);    
      })
      
      if( this.selected.length > 1){
        const multipleSwitch =`
        <div class="changeStage">
          <input type="radio"  class="switch-status--paid" id="paid" name="paidorreject" value="true" checked>
          <label class="switch-status switch-status__paid--color" for="paid"> </label><br>
          <input type="radio" class="switch-status--reject" id="reject" name="paidorreject" value="false">
          <label class="switch-status switch-status__reject--color" for="reject"> </label>
          <div class="switch-status__slider"></div> 
        </div>`;
         row.push(`${multipleSwitch} <hr>`);    
      }

      this.selected = [];
      return `${row.join("")}`
    },
    checkRadio(){
      let check = [];
      let temp = check.push([...document.querySelectorAll('.changeStage input[type="radio"]')]);
      let result = check[0].find(el=> el.checked === true);
      return result.value;
    },
    copyToBuff(el){
      this.copyDone = false
      navigator.clipboard.writeText(`${el}`).then(() =>{
      this.copyDone = true;
      })
    },
    sortedPeriod(dataNum){
       this.numData = dataNum;
    },
    sortTest(a,b,isAsc){
      console.log(a,b,isAsc);
      let num = this.numData;
      if(isAsc === false){
        if(num !== null && num !==undefined){
            this.numisAsc = isAsc;
            return b.reward[num].reward - a.reward[num].reward;
        } 
      }else if(isAsc === true){
        if(num !== null && num !==undefined){
            this.numisAsc = isAsc;
            return a.reward[num].reward - b.reward[num].reward;
        } 
      }
    
    },
    numDataClear(a){
      a ===  'totel' || a === 'id' || a === 'alias' ? this.numData = null : null;
    },
    checkifFiltred(period){
      let checking = this.toCalculatedDate.find(el=> el.end === period.end && el.start === period.start);
      if(checking !== undefined){
        return true;
      } else {
        return false;
      }
    },
    getFullPeriods(){ 
      if(this.periods !== []){
        this.checkedPeriods = [];
        this.checkedPeriods[0]= new Date (new Date(this.periods[0].start).setHours(0));
        this.checkedPeriods[1] = new Date();
      }
    }
}, 
  computed: {
    totalPoolReward() {
      let totalAm = 0;
      
      if(this.snapshot !== null){
      this.snapshot.forEach((el) => {
        if (el.totalPoolReward !== 0) {
          for (let i = 0; i < Object.keys(el.rewardedPeriod).length; i++) {
            totalAm =
              totalAm +
              el.rewardedPeriod[Object.keys(el.rewardedPeriod)[i]].rewardAmount;
          }
        }
      });
      }
      return totalAm.toFixed(8);
    },
    toCalculatedDate(){
      let inputedCheckedPeriodsForCalculating = [];
      let filtredPeriods = [];

      if(this.checkedPeriods !== []){

      let data = this.checkedPeriods;

      if(data[1] === undefined) {
        let tempVal = data;
        data = [];
        data[0] = tempVal;
        data[1] = tempVal;
      } 

      data.map(el => {
        let tempDate = new Date(Date.parse(el));
        let tempYear = tempDate.getFullYear();
        let tempMonth = tempDate.getMonth();
        let tempDay = tempDate.getDate();
        let timezone = tempDate.getTimezoneOffset()/60;
        let tempHour = 9 - timezone;
        console.log(tempHour);

        const newDate = new Date (tempYear, tempMonth, tempDay, tempHour, 0, 0, 0);
        inputedCheckedPeriodsForCalculating.push(newDate.getTime());
      });

      console.log(inputedCheckedPeriodsForCalculating);

      let checkedStartPeriod = this.periods.find(el=> el.start <= inputedCheckedPeriodsForCalculating[0] && inputedCheckedPeriodsForCalculating[0] <= el.end);



      let checkedEndPeriod = this.periods.slice().reverse().find(el=> el.start <=  inputedCheckedPeriodsForCalculating[1] && inputedCheckedPeriodsForCalculating[1] <= el.end);

      let startIndex = this.periods.indexOf(checkedStartPeriod);
      let endIndex = this.periods.indexOf(checkedEndPeriod)+1;
      console.log('index', startIndex, endIndex)
      
      
      if(startIndex !== endIndex){
        filtredPeriods = this.periods.slice(startIndex, endIndex);
      } else if(startIndex !== -1){
        filtredPeriods.push(this.periods[startIndex]);
      }
      
      }

      return filtredPeriods;
    }
    /*
    cleanReward() {
      let result = 0;
      this.periods.forEach((el) => {
        el.poolReward !== undefined && el.poolReward >= 0
          ? (result = result + el.poolReward)
          : null;
      });
      return (result - this.totalPoolReward).toFixed(8);
    },*/
  },
  created() {
    this.loader = this.$buefy.loading.open({container: null});
    if (!firebase.apps.length) {
      this.firebaseConfig = this.$store.getters.getfirebaseConfig;
      firebase.initializeApp(this.firebaseConfig);
    }
    

    let database = firebase.database();
    let poolClients = database.ref("/firstline");
    let poolPeriods = database.ref("/periods");
    let poolSnapshots = database.ref("/pool");
    let poolCurrentPeriod = database.ref("/currentPeriod");


    poolCurrentPeriod.once('value').then( snap =>{
      let tempSnap = snap.val();
      this.miningPoolFullIncome = tempSnap[Object.keys(tempSnap)[0]].miningPoolFullReward;
      this.miningPoolAlias = tempSnap[Object.keys(tempSnap)[0]].assetboxData.alias;
    });

    poolClients.once("value").then((snapshot) => {
      let tempSnap = snapshot.val();
      let tempSnapAssetboxesData = [];
      let tempClientsList = [];

      if (tempSnap !== 0) {
        for (let i = 0; i < Object.keys(tempSnap).length; i++) {
          tempSnapAssetboxesData.push(tempSnap[Object.keys(tempSnap)[i]]);
        }
        tempSnapAssetboxesData.forEach((el) => {
          tempClientsList.push({ adress: el.adress, alias: el.alias });
        });
      }

      this.firstlineAssets = tempClientsList;
      this.snapshot = tempSnapAssetboxesData;

      poolPeriods
        .once("value")
        .then((snap) => {
          let tempPeriodsforRendiring = [];
          let tempSnap = snap.val();

          if (tempSnap !== 0) {
            for (let i = 0; i < Object.keys(tempSnap).length; i++) {
              tempPeriodsforRendiring.push(tempSnap[Object.keys(tempSnap)[i]]);
            }
          }

          tempPeriodsforRendiring.forEach((element) => {
            let tempReward = 0;
            if (element.rewardData !== 0 && element.rewardData !== undefined) {
              element.rewardData.totalReward !== undefined
                ? (tempReward = element.rewardData.totalReward)
                : null;
            }

            this.periods.push({
              start: element.start,
              end: element.end,
              poolReward: tempReward,
            });
          });
        })
        .then(() => {
          poolSnapshots.once("value").then((res) => {
            let tempRes = res.val();
            Object.keys(tempRes).length - 1 >= 0
              ? (this.time =
                  tempRes[
                    Object.keys(tempRes)[Object.keys(tempRes).length - 1]
                  ].time)
              : (this.time = 0);
          });
        });
    });

    poolClients.once("value").then((snapshot) => {
      let tempSnap = snapshot.val();
      let tempSnapAssetboxesData = [];
      let tempClientsList = [];

      if (tempSnap !== 0) {
        for (let i = 0; i < Object.keys(tempSnap).length; i++) {
          tempSnapAssetboxesData.push(tempSnap[Object.keys(tempSnap)[i]]);
        }
        tempSnapAssetboxesData.forEach((el) => {
          tempClientsList.push({ adress: el.adress, alias: el.alias });
        });
      }

      this.firstlineAssets = tempClientsList;
      this.snapshot = tempSnapAssetboxesData;

      //new
      poolSnapshots
        .once("value")
        .then((res) => {
          let tempRes = res.val();
          // console.log(Object.keys(tempRes));
          Object.keys(tempRes).length - 1 >= 0
            ? (this.time =
                tempRes[
                  Object.keys(tempRes)[Object.keys(tempRes).length - 1]
                ].time)
            : (this.time = 0);
        })
        .then(() => {
          tempClientsList.forEach((el, index) => {
            let rewforPeriods = [];

            poolPeriods.once("value").then((snap) => {
              let tempPeriodsforRendiring = [];
              let tempSnap = snap.val();

              if (tempSnap !== 0) {
                for (let i = 0; i < Object.keys(tempSnap).length; i++) {
                  tempPeriodsforRendiring.push(
                    tempSnap[Object.keys(tempSnap)[i]]
                  );
                }
              }

              tempPeriodsforRendiring.forEach((period, index) => {
                let startofPeriod = period.start;
                let endofPeriod = period.end;

                let checked = this.snapshot.find(
                  (element) => element.adress == el.adress
                );

                if (this.time > period.end) {
                  if (checked !== undefined) {
                    if (
                      checked.rewardedPeriod !== undefined &&
                      checked.rewardedPeriod !== 0
                    ) {
                      for (
                        let i = 0;
                        i <= Object.keys(checked.rewardedPeriod).length - 1;
                        i++
                      ) {
                        let checkedEl =
                          checked.rewardedPeriod[
                            Object.keys(checked.rewardedPeriod)[i]
                          ];

                        if (
                          checkedEl.start === startofPeriod &&
                          checkedEl.end === endofPeriod
                        ) {
                          rewforPeriods.push({
                            i: index,
                            reward: checkedEl.rewardAmount,
                            periodStart: startofPeriod,
                          });
                          return;
                        } else if (
                          i ==
                          Object.keys(checked.rewardedPeriod).length - 1
                        ) {
                          rewforPeriods.push({
                            i: index,
                            reward: 0,
                            periodStart: startofPeriod,
                          });
                          return;
                        }
                      }
                    } else {
                      rewforPeriods.push({
                        i: index,
                        reward: 0,
                        periodStart: startofPeriod,
                      });
                    }
                  } else {
                    rewforPeriods.push({
                      i: index,
                      reward: 0,
                      periodStart: startofPeriod,
                    });
                  }
                } else {
                  rewforPeriods.push({
                    i: index,
                    reward: "-",
                    periodStart: startofPeriod,
                  });
                }
              });
              this.data.unshift({
                id: index,
                alias: el.alias,
                asset: el.adress,
                reward: rewforPeriods,
                totel: 0,
              });
            });
          });
        }).then(_=>{
          setTimeout(() => this.loader.close(),1000);
        })
    });
    
    let tempPrevMonth;
    const tempDataforFilter = new Date();
    const defaultFilterEnd = new Date(tempDataforFilter);

    tempDataforFilter.getMonth() == 0 ? tempPrevMonth = 11 : tempPrevMonth = tempDataforFilter.getMonth() - 1;
   
    const defaultFilterStart = new Date( tempDataforFilter.setMonth(tempPrevMonth));
    this.checkedPeriods[0]=defaultFilterStart;
    this.checkedPeriods[1]=defaultFilterEnd;
    /*
      poolPeriods.once("value").then((snap) => {
        let tempPeriodsforRendiring = [];
        let tempSnap = snap.val();

        if (tempSnap !== 0) {
          for (let i = 0; i < Object.keys(tempSnap).length; i++) {
            tempPeriodsforRendiring.push(tempSnap[Object.keys(tempSnap)[i]]);
          }
        }

        tempPeriodsforRendiring.forEach((element) => {
          let tempReward = 0;
          if (element.rewardData !== 0 && element.rewardData !== undefined) {
            element.rewardData.totalReward !== undefined
              ? (tempReward = element.rewardData.totalReward)
              : null;
          }

          this.periods.push({
            start: element.start,
            end: element.end,
            poolReward: tempReward,
          });
        });
      }).then(()=>{
        poolSnapshots.once('value').then(res => {
          let tempRes = res.val();
          console.log(Object.keys(tempRes));
            Object.keys(tempRes).length-1 >= 0
            ? this.time = tempRes[Object.keys(tempRes)[Object.keys(tempRes).length-1]].time
            : this.time = 0;
        })
      })
    });*/
  },
};
</script>

